@import url(https://fonts.googleapis.com/css?family=Orbitron:700&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  --light-background-color: #bdbdbd;
  --dark-background-color: #7e7e7e;
}

html, body, .App, .root{
  height: 100vh;
}

body{
  display: flex;
  align-items: center;
  justify-content: center;
}

.dark{
  background: #7e7e7e !important;
  background: var(--dark-background-color) !important;
}

.App{
  background: #bdbdbd;
  background: var(--light-background-color);
  border: outset;
  padding: 25px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.game-board{
    border: inset;
    height: 50%;
    width: 50%;
    display: grid;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background: var(--light-background-color);
}

.tile{
    border: outset;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--light-background-color);
    outline: none;
}

.inset{
    border: inset;
}

.no-bomb{
    background: grey;
}

.header, .footer{
    width: 100%;
    height: 75px;
    background: #c0c0c0;
    border: inset;
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.footer{
    margin-bottom: 0;
    margin-top: 15px;
    justify-content: space-around;
    height: 60px;
}

.header__button{
    width: 50px;
    height: 50px;
    font-size: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border: outset;
    outline: none;
    line-height: 1;
    background: inherit;
}
.header__button:active {
    border: inset
}

.emoji{
    height: -webkit-fit-content !important;
    height: -moz-fit-content !important;
    height: fit-content !important;
}

.emoji *{
    height: -webkit-fit-content !important;
    height: -moz-fit-content !important;
    height: fit-content !important;
    margin: 0 !important;
    width: -webkit-fit-content !important;
    width: -moz-fit-content !important;
    width: fit-content !important;
    vertical-align: 0 !important;
}

.header__text{
    font-family: 'Orbitron',
    sans-serif;
    font-size: 30px;
    color: red;
    background: black;
    margin-right: 10px;
    margin-left: 10px;
    width: 125px;
    text-align: center;
    padding: 0 5px 0 5px;
}

.footer__section{
    display: flex;
    flex-direction: column;
}

.footer__section * {
    margin: 2.5px;
}

